import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JsonFormatComponent } from './components/json-format.component';
import { JsonToSchemaComponent } from './components/json-to-schema.component';
import { JsonToYamlComponent } from './components/json-to-yaml';
import { JsonToXmlComponent } from './components/json-to-xml';


const routes: Routes = [
  {
    path: 'format', component: JsonFormatComponent,
  },
  {
    path: 'to', children: [
      { path: 'schema', component: JsonToSchemaComponent},
      { path: 'yaml', component: JsonToYamlComponent},
      { path: 'xml', component: JsonToXmlComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class JsonRoutingModule { }
