import { TransformToolComponent } from 'src/app/core/transform-tool/transform-tool.component';

export class JsonFormatComponent extends TransformToolComponent {
  title = 'JSON Format';
  action = 'Format';
  schema = {
    type: 'object',
    properties: {
      input: { type: 'string', title: 'JSON', description: 'JSON to format' },
      advanced: {
        type: 'object',
        properties: {
          whitespaceType: { type: 'string', enum: ['space', 'tab'], default: 'space', description: 'New line indent format' },
          size: { type: 'number', default: 3, description: 'Number of spaces to indent with' }
        },
        required: ['whitespaceType'],
        "x-schema-form": {
          type: "section",
          expandable: true,
          expande: false,
          title: 'Advanced'
        }
      }
    },
    required: ['input']
  }

  onTransform(transform: any) {
    const input = transform.input;
    const options = transform.advanced;
    var object = JSON.parse(input);
    var format = options.whitespaceType == 'space' ? options.size : '\t';
    return JSON.stringify(object, null, format);
  }

  documentation = `
  JSON is often minified or uglified to reduce the size of the JSON. 
  Minifying is the process of removing whitespace and other unneccassary characters.
  Web pages load faster when load sizes are smaller, so this minification process is often used in web development.
  However, the minified versions are harder to read by humans.

  This tool will quickly format a json document to make it easily readable.
  `;
}
