import { TransformToolComponent } from 'src/app/core/transform-tool/transform-tool.component';
import yaml from 'yaml';

export class JsonToYamlComponent extends TransformToolComponent {
    title = 'Convert JSON to YAML';
    action = 'Convert';
    schema = {
        type: 'object',
        properties: {
            input: { type: 'string', title: 'JSON', description: 'JSON to convert' }
        },
        required: ['input']
    };

    onTransform(transform: any): string {
        const result = yaml.parse(transform.input);
        return yaml.stringify(result);
    }

    documentation = `
    This tool will convert a JSON document to YAML.
    `;

}