import { TransformToolComponent } from 'src/app/core/transform-tool/transform-tool.component';
import jsonToXml from 'jsontoxml';

export class JsonToXmlComponent extends TransformToolComponent {
    title = 'Convert JSON to XML';
    action = 'Convert';
    schema = {
        type: 'object',
        properties: {
            input: { type: 'string', title: 'JSON', description: 'JSON to Convert' },
            advanced: {
                type: 'object',
                properties: {
                    xmlHeader: { type: 'boolean', description: 'Add XML header' },
                    prettyPrint: { type: 'boolean', default: true, description: 'Make XML human readable' }
                },
                "x-schema-form": {
                    type: "section",
                    expandable: true,
                    expande: false,
                    title: 'Advanced'
                }
            }
        },
        required: ['input']
    };

    onTransform(transform: any): string {
        return jsonToXml(transform.input, transform.advanced);
    }

    documentation = `
    This tool will convert a JSON document to XML.
    `;


}